:root {
  --bg: #f7f7fb;
  --panel: #ffffff;
  --text: #1f2330;
  --muted: #6b7280;
  --line: #e5e7eb;
  --now: #ef4444;
  --chip-bg: #eef2ff;
  --chip-text: #3730a3;
  --shadow: 0 1px 4px rgba(0,0,0,0.08), 0 6px 12px rgba(0,0,0,0.06);
}

/* Viewport scrollable pour la grille (évite que les cases “débordent” l’écran) */
.grid-scroll {
  max-height: calc(100vh - 220px); /* ajuste si besoin */
  overflow: auto;
  overscroll-behavior: contain; /* avoid body scroll chaining */
}

/* Utilise une variable CSS pour la densité verticale (px par minute) */
:root {
  /* ...existing code... */
  --px-per-min: 1.2;
  --hours-col-w: 72px;
  --day-min-w: 240px;
  --col-gap: 8px;
  --col-pad: 6px;

  /* nouvelle variable: hauteur de l’entête de jour */
  --day-head-h: 36px;
}

/* Colonne heures: réserve l’espace de l’entête pour aligner les lignes */
.hours-col {
  background: var(--panel);
  border: 1px solid var(--line);
  border-radius: 8px;
  padding: 8px 0;
  padding-top: var(--day-head-h);
  position: sticky;   /* <- reste visible */
  left: 0;
  z-index: 3;         /* au-dessus des jours */
}

/* 1 ligne par heure, hauteur = 60 min * px/min */
.hour-label {
  height: calc(60 * var(--px-per-min) * 1px);
  font-size: 12px;
  color: var(--muted);
  padding-left: 10px;
  box-sizing: border-box;
  position: relative;
}
.hour-label::after {
  content: '';
  position: absolute;
  right: 8px;
  left: 8px;
  bottom: 0;
  height: 1px;
  background: var(--line);
  opacity: 0.6;
}

/* En-tête jour à hauteur fixe */
.day-header {
  height: var(--day-head-h);
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 0;
  background: var(--panel);
  color: var(--text);
  text-align: center;
  font-weight: 600;
  padding: 8px 0;
  border-bottom: 1px solid var(--line);
  z-index: 1;
}

/* La colonne jour ne porte plus la grille -> seulement le cadre */
.day-col {
  position: relative;
  height: calc(660 * var(--px-per-min) * 1px);
  border: 1px solid var(--line);
  border-radius: 8px;
  background-color: var(--panel);
  overflow: visible; /* pour le zoom */
}

/* La grille horaire est sur le contenu, pile sous l’entête */
.day-content {
  position: relative;
  height: calc(100% - var(--day-head-h));
  padding: 0 var(--col-pad);
  overflow: visible;

  /* grille heures: 1 trait par heure, parfaitement aligné avec 08:00 en haut */
  background:
    repeating-linear-gradient(
      to bottom,
      transparent 0,
      transparent calc(60 * var(--px-per-min) * 1px - 1px),
      var(--line) calc(60 * var(--px-per-min) * 1px - 1px),
      var(--line) calc(60 * var(--px-per-min) * 1px)
    );
}

/* Cases */
.slot {
  box-sizing: border-box;
  position: absolute;
  /* top/height pilotés par variables pour pouvoir les overrider au hover */
  top: var(--slot-top);
  height: var(--slot-height);

  padding: 8px 10px;
  border-radius: 8px;
  color: var(--text);
  box-shadow: var(--shadow);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 4px;
  border-left: 3px solid rgba(0,0,0,0.1);
  left: var(--slot-left);
  width: var(--slot-width);

  transition: transform 120ms ease, box-shadow 120ms ease, left 80ms ease, width 80ms ease,
              height 120ms ease; /* transitionne la hauteur */
  transform-origin: center center;
}
.slot:hover,
.slot:focus-within {
  transform: scale(1.02);
  z-index: 9999;
  box-shadow: 0 6px 18px rgba(0,0,0,0.25), 0 2px 8px rgba(0,0,0,0.2);
  left: var(--col-pad);
  width: calc(100% - (var(--col-pad) * 2));

  /* grandit en hauteur si trop petit (utile pour les cours d’1h) */
  height: max(var(--slot-height), var(--slot-hover-min-h));
  overflow: visible; /* laisse sortir le contenu si nécessaire */
}

/* Actions en overlay (masquées par défaut) */
.slot-controls {
  position: absolute;
  right: 6px;
  bottom: 6px;
  display: flex;
  gap: 6px;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: opacity 120ms ease;
}
.slot:hover .slot-controls,
.slot:focus-within .slot-controls {
  opacity: 1;
  pointer-events: auto;
}

/* Évite que le select prenne toute la largeur en petit slot */
.slot-controls select {
  max-width: 160px;
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 6px;
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: #0f1117;
    --panel: #151923;
    --text: #e5e7eb;
    --muted: #9aa1af;
    --line: #222632;
    --now: #f87171;
    --chip-bg: #1e293b;
    --chip-text: #c7d2fe;
    --shadow: 0 1px 4px rgba(0,0,0,0.5), 0 6px 14px rgba(0,0,0,0.45);
  }
}

.schedule-wrap {
  background: var(--bg);
  padding: 16px;
  border-radius: 8px;
}

.week-head {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.week-head h2 {
  margin: 0;
  color: var(--text);
}

.week-head .actions {
  margin-left: auto;
  display: flex;
  gap: 8px;
}

.week-btn {
  background: var(--panel);
  color: var(--text);
  border: 1px solid var(--line);
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}

